import { get, post, update, remove } from './Methods'

const baseURL = 'http://localhost:3006/api/v1/user';

const UserApis = {
    createUser: Function,
    login: Function,
    getUsers: Function,
    getUserById: Function,
    updateUser: Function,
    deleteUser: Function
}


UserApis.createUser = (body) => {
    let url = baseURL + '/create';
    return post(url, body).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

UserApis.login = (body) => {
    let url = baseURL + '/login';
    return post(url, body).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

UserApis.getUsers = () => {
    let url = baseURL + '/all';
    return get(url).then(response => response).catch(error => {
        console.error('There was an error!', error);
    });
}

UserApis.getUserById = (id) => {
    let url = baseURL + '/' + id;
    return get(url).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

UserApis.updateUser = (body, id) => {
    let url = baseURL + '/' + id;
    return update(url, body).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

UserApis.deleteUser = (id) => {
    let url = baseURL + '/' + id;
    return remove(url).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

export default UserApis;