import React, { useContext, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { SocketContext } from '../../../context/SocketContext';
import { Box, Fab, Grid, Paper, Typography } from '@material-ui/core';
import '../../../scss/Video.scss';
import VideocamOutlinedIcon from '@material-ui/icons/VideocamOutlined';
import VideocamOffOutlinedIcon from '@material-ui/icons/VideocamOffOutlined';
import MicNoneOutlinedIcon from '@mui/icons-material/MicNoneOutlined';
import MicOffOutlinedIcon from '@mui/icons-material/MicOffOutlined';
import ScreenShareOutlinedIcon from '@mui/icons-material/ScreenShareOutlined';
import { useParams } from "react-router-dom";


function VideoPlayer() {
    const { id } = useParams();
    const { name, setName, myVideo, userStream, audio, video, audioOnOff, videoOnOff, 
        shareMyScreenScreen , ActivateMyVideo} = useContext(SocketContext);
        useEffect(() => {
            ActivateMyVideo();
        }, [])

    return (
        <div>
            {userStream && (
                <Paper className="paper" elevation={4}>
                    <video playsInline muted ref={myVideo} autoPlay className="videoframe" />
                    <Box display="flex" justifyContent="space-between" width="50%" margin="auto" className="controls">

                        {
                            audio ?
                                <Fab size="small" color="secondary" aria-label="Audio" onClick={audioOnOff}>
                                    <MicNoneOutlinedIcon />
                                </Fab> :
                                <Fab size="small" color="secondary" aria-label="Audio" onClick={audioOnOff}>
                                    <MicOffOutlinedIcon />
                                </Fab>
                        }
                        {
                            video ?
                                <Fab size="small" color="secondary" aria-label="Video" onClick={videoOnOff}>
                                    <VideocamOutlinedIcon />
                                </Fab> :
                                <Fab size="small" color="secondary" aria-label="Video" onClick={videoOnOff}>
                                    <VideocamOffOutlinedIcon />
                                </Fab>
                        }

                        <Fab size="small" color="secondary" aria-label="Video" onClick={()=>{shareMyScreenScreen(id)}}>
                            <ScreenShareOutlinedIcon />
                        </Fab>
                    </Box>
                </Paper>
            )}
          
        </div>
    )
}

export default VideoPlayer
