import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { CodeingContextProvider } from './context/CodeingContext'
import Home from './routes/Home';
import CodeEditor from './components/Logged-In/Codeing/CodeEditor';
import LoggedIn from './routes/LoggedIn';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Box, Typography } from '@material-ui/core';
import ProjectView from './routes/ProjectView';

function App() {
  const pageNotFound = () => {
    return (
      <Box mt={5}>
        <Typography variant="h4" display="block" align="center" gutterBottom >
          Page Not Found
        </Typography>
      </Box>
    )
  }
  return (
    <CodeingContextProvider>
      <ToastContainer position="top-right" autoClose={5000} hideProgressBar={false} newestOnTop={false} closeOnClick
        rtl={false} pauseOnFocusLoss draggable pauseOnHover />
      <Router>
        <Switch>
          <Route exact path="/" component={Home}></Route>
          <Route exact path="/code-editor" component={CodeEditor}></Route>
          <Route exact path="/logged-in/*" component={LoggedIn}></Route>
          <Route exact path="/project/*/:id" component={ProjectView}></Route>
          {/* <Route exact path="/project/join/:id" component={ProjectView}></Route> */}

          <Route component={pageNotFound} />
        </Switch>
      </Router>
      <ToastContainer />
    </CodeingContextProvider>
  );
}

export default App;
