import { toast } from 'react-toastify';

const Toast = {
  options: {
    position: "top-right",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  },
  success: function (msg) {
    toast.success(msg, this.options)
  },
  warning: function (msg) {
    toast.warning(msg, this.options)
  },
  info: function (msg) {
    toast.info(msg, this.options)
  },
  error: function (msg) {
    toast.error(msg, this.options)
  },
  dark: function (msg) {
    toast.dark(msg, this.options)
  },
  default: function (msg) {
    toast.default(msg, this.options)
  },
  

};


export default Toast;