import React from 'react';
import { Route } from 'react-router';
import ProjectLayout from '../components/Logged-In/ProjectView/ProjectLayout';
import JoinProject from '../components/Logged-In/ProjectView/JoinProject';
import { SocketContextProvider } from "../context/SocketContext";

function ProjectView() {
    return (
        <SocketContextProvider>
            <Route exact path='/project/start/:id'>
                <ProjectLayout></ProjectLayout>
            </Route>
            <Route exact path='/project/join/:id'>
                <JoinProject></JoinProject>
            </Route>
        </SocketContextProvider>
    )
}

export default ProjectView
