import React from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import { Button, Container } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import UserInfo from '../../hooks/UserInfo';

const useStyles = makeStyles((theme) => ({
    navbarContentPadding: {
        padding: theme.spacing(8, 0, 0, 0),
    },
    grow: {
        flexGrow: 1,
    },

    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
    },

    navItem: {
        padding: theme.spacing(1, 2.5),
    }

}));


export default function Navbar() {
    const classes = useStyles();
    const history = useHistory();

    const routeTo = (path) => {
        history.push({ pathname: path});
    }

    const logout = (e) =>{
        e.preventDefault();
        UserInfo.clearUser();
        history.push('/');
    }


    return (
        <div className={classes.navbarContentPadding}>
            <AppBar position="fixed" color="primary">
                <Container>
                <Toolbar>

                    <Typography variant="h4" noWrap>
                        Smart C
                   </Typography>

                    <div className={classes.search}>
                        <div className={classes.searchIcon}>
                            <SearchIcon />
                        </div>
                        <InputBase
                            placeholder="Search…"
                            classes={{
                                root: classes.inputRoot,
                                input: classes.inputInput,
                            }}
                            inputProps={{ 'aria-label': 'search' }}
                        />
                    </div>

                    <div className={classes.grow} />
                    <div>
                        <Button className={classes.navItem} color="inherit" onClick={() => routeTo('/logged-in/dashboard')}>Dashboard</Button>
                        <Button className={classes.navItem} color="inherit" onClick={() => routeTo('/logged-in/my-projects')}>My Projects</Button>
                        <Button className={classes.navItem} color="inherit">Direct Practice</Button>
                        <Button className={classes.navItem} color="inherit" onClick={() => routeTo('/logged-in/block-codeing')} >Block Codeing</Button>
                        <Button className={classes.navItem} color="inherit" onClick={() => routeTo('/logged-in/profile')}>Profile</Button>
                        <Button className={classes.navItem} color="inherit" onClick={logout}>Logout</Button>
                    </div>

                </Toolbar>
                </Container>
            </AppBar>

        </div>
    );
}
