import React, { useContext} from 'react'
import FileStructure from './FileStructure'
import '../../../scss/ProjectLayout.scss';
import Split from 'react-split'
import CodePannel from '../Codeing/CodePannel';
import { Box, Button } from '@material-ui/core';
import { useState } from 'react';
import VideoPlayer from '../Video/VideoPlayer';
import { CodeingContext } from '../../../context/CodeingContext';
import Output from '../Codeing/Output';
 

function ProjectLayout() {
  const [verticalGutterSizes, setVerticalGutterSizes] = useState([95, 5]);
  const [BtnActive, setBtnActive] = useState('');
  const { file } = useContext(CodeingContext);

  return (
    <div className="myCodeLayout">
      <Split className="split" sizes={[20, 80]} minSize={320} expandToMin={false} gutterSize={2}
        dragInterval={1} cursor="col-resize" >
        <Box>
          <FileStructure></FileStructure>
          <VideoPlayer></VideoPlayer>
        </Box>
        <Split className="innerSplit" sizes={verticalGutterSizes} minSize={38} expandToMin={true} gutterSize={2}
          dragInterval={1} direction="vertical" cursor="col-resize" >
          <CodePannel></CodePannel>
          <Box component="div" className="OutputLayout">
            <Box component="div" display="flex" justifyContent="space-between" borderBottom={1} borderColor="lightgray">
              <Box>
                <Button variant={BtnActive === 'output' ? 'outlined' : 'text'} color="primary" onClick={() => { setVerticalGutterSizes([5, 95]); setBtnActive('output') }}>Output</Button>
                {/* <Button variant={BtnActive === 'console' ? 'outlined' : 'text'} color="primary" onClick={() => { setVerticalGutterSizes([5, 95]); setBtnActive('console') }} >Console</Button> */}
              </Box>
              <Button color="secondary" onClick={() => { setVerticalGutterSizes([95, 5]); setBtnActive('') }}>Close</Button>
            </Box>
            <Box>
              <Output file={file}></Output>
            </Box>

          </Box>
        </Split>
      </Split>

    </div>
  )
}

export default ProjectLayout;
