import { Box, Typography } from '@material-ui/core'
import React from 'react'
import { fade, makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    myHeader: {
        height: '350px',
        background: 'linear-gradient(to right, rgba(0, 0, 0, 0.1) 1px, transparent 1px) 0% 0% / 40px 40px, linear-gradient(rgba(0, 0, 0, 0.1) 1px, transparent 1px) rgb(54, 54, 69)'
    },
    heading:{
        color:'white'
    }
})
)

function HeaderStrip({title}) {
    const classes = useStyles();

    return (
        <div>
            <Box className={classes.myHeader} display="flex" justifyContent="center" alignItems="center">
                <Typography variant="h3" gutterBottom className={classes.heading}>
                    {title}
                </Typography>
            </Box>
        </div>
    )
}

export default HeaderStrip
