import React from 'react'
import { Box, Card, CardContent, CardActions, Button, Container, Grid, TextField } from '@material-ui/core';

import './LandingStyles.scss'
import Register from './Register';
import Login from './Login';

function Landing() {
    return (
        <div>
            <Box component="div" className="homeBg">
                <Container>
                    <Grid container direction="row" justify="flex-end" >
                        <Grid item md={10}>
                            <Card className="registerCard">
                                <CardContent>
                                    <Register></Register>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                    <Grid container direction="row" justify="flex-start" >
                        <Grid item md={10}>
                            <Card className="loginCard">
                                <CardContent>
                                    <Login></Login>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                </Container>

            </Box>
        </div>
    )
}

export default Landing
