import React, { useState, useEffect, useContext } from 'react'
import AceEditor from "react-ace";
import "ace-builds/src-noconflict/theme-monokai";
import "ace-builds/src-noconflict/ext-language_tools";
import "ace-builds/src-noconflict/ext-beautify";
import "ace-builds/src-noconflict/mode-html";
import "ace-builds/src-noconflict/mode-xml";
import "ace-builds/src-noconflict/mode-css";
import "ace-builds/src-noconflict/mode-scss";
import "ace-builds/src-noconflict/mode-java";
import "ace-builds/src-noconflict/mode-javascript";
import "ace-builds/src-noconflict/mode-json";
import "ace-builds/src-noconflict/mode-python";
import "ace-builds/src-noconflict/mode-sql";
import "ace-builds/src-noconflict/mode-pgsql";
import "ace-builds/src-noconflict/mode-ejs";
import Smart_C_Apis from '../../../apis/Coading'
import useLocalStorage from '../../../hooks/useLocalStorage';
import { CodeingContext } from '../../../context/CodeingContext';



var codeChangeCounter = 0;
var intrvals = [];
var FileContent = '';
function CodeEditor({ file }) {

    const [CoadText, setCoadText] = useState(null);
    const [refreshAce, setRefreshAce] = useState(false);
    const {  setMode, setCode } = useContext(CodeingContext);


    useEffect(() => {
        if (file.fileId) {
            Smart_C_Apis.getFile(file.fileId).then(response => {
                console.log(response)
                if (response.status === 200) {
                    response.data && setCoadText(response.data.filecontent)
                    FileContent = response.data.filecontent;
                } else {
                    setCoadText('');
                    FileContent = "";
                }
            })
        } else {
            setCoadText('');
            FileContent = "";
        }
        setRefreshAce(true);
        setMode(file.mode)
    }, [file])

    useEffect(() => {
        setCode(CoadText)
    }, [CoadText])

    const onChnageCode = (e) => {
        setCoadText(e)
        FileContent = e;
        codeChangeCounter = 0;
        intrvals.forEach((intr, index) => {
            clearInterval(intr);
            intrvals.splice(index, 1)
        });
        var saveinterval = setInterval(() => {
            codeChangeCounter++
            if (codeChangeCounter > 1) {
                Smart_C_Apis.updateFileContent({ fileContent: FileContent ? FileContent : '' }, file.fileId).then(response => {
                    console.log("updateFileContent==>", response);
                });
                codeChangeCounter = 0;
                clearInterval(saveinterval);
            }
        }, 1000);
        intrvals.push(saveinterval);

    }


    return (
        <div>
            {refreshAce ? <AceEditor placeholder="Code here" mode={file.mode} theme="monokai" name={file.name}
                onChange={(e) => onChnageCode(e)} fontSize={16} showPrintMargin={true} showGutter={true}
                highlightActiveLine={true} value={CoadText}
                editorProps={{ $blockScrolling: true }} enableSnippets={true}
                height="Calc( 100vh - 80px )"
                width="100%"
                setOptions={{
                    enableBasicAutocompletion: true,
                    enableLiveAutocompletion: true,
                    enableSnippets: true,
                    showLineNumbers: true,
                }} /> : ''}
        </div>
    )
}

export default CodeEditor
