import EncrDecr from "./EncrDecr"

const addEditorTabsAndContent = {
    addAndGetFile: Function,
    addFileContent: Function,
    removeTabs: Function,
    getCompilerLanguage: Function
}

var Tabs = [];
addEditorTabsAndContent.addAndGetFile = (file) => {
    const tabValues = localStorage.getItem('tabs');
    if (file) {
        file.active = true;
        if (tabValues) {
            Tabs = JSON.parse(EncrDecr.get(localStorage.getItem('tabs')));
            var tabExist = false;
            Tabs.forEach((tab, index) => {
                tab.active = false;
                if (tab.fileId == file.fileId) {
                    tabExist = true;
                    Tabs.splice(index, 1, file);
                }
            });
            console.log("tabExist", tabExist);

            if (!tabExist) {
                Tabs.push(file);
            }
            localStorage.setItem('tabs', EncrDecr.set(JSON.stringify(Tabs)));
        } else {
            Tabs.push(file);
            localStorage.setItem('tabs', EncrDecr.set(JSON.stringify(Tabs)));
        }

        return JSON.parse(EncrDecr.get(localStorage.getItem('tabs')));
    } else {
        if (tabValues) {
            return JSON.parse(EncrDecr.get(localStorage.getItem('tabs')));
        }
        else {
            return [];
        }
    }
}


addEditorTabsAndContent.removeTabs = (file) => {
    const tabValues = localStorage.getItem('tabs');
    if (file) {
        if (tabValues) {
            Tabs = JSON.parse(EncrDecr.get(localStorage.getItem('tabs')));
            Tabs.forEach((tab, index) => {
                if (file.active) {
                    index === 0 ? tab.active = true : tab.active = false;
                }
                if (tab.fileId === file.fileId) {
                    Tabs.splice(index, 1);
                }
            })

            localStorage.setItem('tabs', EncrDecr.set(JSON.stringify(Tabs)));

        }
    }

    return JSON.parse(EncrDecr.get(localStorage.getItem('tabs')));
}


addEditorTabsAndContent.getCompilerLanguage = (mode) => {
    let language = "node"
    switch (mode) {
        case "javascript":
            language = "node";
            break;

        case "c":
            language = "c";
            break;

        case "py":
            language = "python";
            break;

        case "cpp":
            language = "cpp";
            break;

        case "java":
            language = "java";
            break;
    }
    return language;
}





export default addEditorTabsAndContent;
