import EncrDecr from "./EncrDecr"

const UserInfo = {
    setUser: Function,
    getUser: Function,
    clearUser: Function,
    isLoggedIn: Function
}

UserInfo.setUser = (user) => {
   var encriptedData = EncrDecr.set(JSON.stringify(user))
    localStorage.setItem('userInfo', encriptedData)
}

UserInfo.getUser = () =>{
    if(localStorage.getItem('userInfo')){
     const user = JSON.parse(EncrDecr.get(localStorage.getItem('userInfo')));
     if(user){
        return user;
     }else{
        return null
     }
    }else{
        return null
    }
}

UserInfo.clearUser = () =>{
  localStorage.removeItem('userInfo')
}

UserInfo.isLoggedIn = () =>{
    return !!localStorage.getItem('userInfo');
}


export default UserInfo;