import React, { useEffect, useState, useContext } from 'react'
import { CodeingContext } from '../../../context/CodeingContext';
import CompilerApis from '../../../apis/Compile'
import addEditorTabsAndContent from '../../../hooks/SetEditorTabsAndContent'

function Output() {
    const [output, setOutput] = useState("...")
    const { mode,  code } = useContext(CodeingContext);

    // const { srcDoc } = props;

    useEffect(() => {
            let language = addEditorTabsAndContent.getCompilerLanguage(mode)
            let reqData = {
                code: code,
                language: language,
                input: ""
            }
            CompilerApis.compile(reqData).then(response => {
                console.log(response)
                if (response.status === 200) {
                    setOutput("");
                    if (response.data.stderr) {
                        setOutput(response.data.stderr)
                    }
                    if (response.data.stdout) {
                        setOutput(response.data.stdout)
                    }
                } 
            })
    }, [code])

    return (
        <div>
          <pre className="preoutput"> {output} </pre>
        </div>

        // document.getElementById('frame').contentWindow.console = console;
    )
}

export default Output;
