import React, { useContext, useEffect } from 'react'
import { SocketContext } from '../../../context/SocketContext';
import { Box, Fab, Grid, Paper, Typography, Button } from '@material-ui/core';
import '../../../scss/Video.scss';
import { useParams } from "react-router-dom";

function ScreenShareVideo() {
    const { name, setName, joinScreenShare, screenShareVideo } = useContext(SocketContext);
    const { id } = useParams();
    // useEffect(() => {
    //     
    // }, [])

    return (
        <div>
            <Button color="default" onClick={() => { joinScreenShare(id) }}>
                Connect
            </Button>
            hello shiva krishna
            <Paper elevation={4}>
                <video playsInline ref={screenShareVideo} autoPlay className="screenShareVideo" />
            </Paper>
        </div>
    )
}

export default ScreenShareVideo
