import React, { createContext, useState } from "react";


export const CodeingContext = createContext();

export const CodeingContextProvider = props => {

    const [users, setUsers] = useState([]);
    const [user, setUser] = useState({});
    
    const addUserInUsers = (user) => {
        setUsers((users) => {
            if (users) {
                return [...users, user]
            } else {
                return [user]
            }
        });
    }

    const [projects, setProjects] = useState([]);
    const [project, setProject] = useState({});
    const addProjectInProjects = (project) => {
        setProjects((projects) => {
            if (projects) {
                return [...projects, project]
            } else {
                return [project]
            }
        });
    }

    const [folders, setFolders] = useState([]);
    const [folder, setFolder] = useState({});
    const addFolderInFolders = (folder) => {
        setFolders((folders) => {
            if (folders) {
                return [...folders, folder]
            } else {
                return [folder]
            }
        });
    }

    const [files, setFiles] = useState([]);
    const [file, setFile] = useState({});
    const addFileInFiles = (file) => {
        setFiles((files) => {
            if (files) {
                return [...files, file]
            } else {
                return [file]
            }
        });
    }

    const [tabs, setTabs] = useState([]);
    const [tab , setTab] = useState({})
    const addTabInTabs = (file) => {
        setTabs((files) => {
            if (files) {
                return [...files, file]
            } else {
                return [file]
            }
        });
    }

    const [mode, setMode] = useState("node");
    const [code , setCode] = useState("")
    

    return (
        <CodeingContext.Provider
            value={{
                users, setUsers,
                user, setUser,
                addUserInUsers,

                projects, setProjects,
                project, setProject,
                addProjectInProjects,

                folders, setFolders,
                folder, setFolder,
                addFolderInFolders,

                files, setFiles,
                file, setFile,
                addFileInFiles,

                tabs, setTabs,
                tab , setTab,
                addTabInTabs,
                mode, setMode,
                code , setCode
            }}
        >
            {props.children}
        </CodeingContext.Provider>
    )

}
