import { Container, Box, Grid, IconButton, Button, Typography, TextField, Card } from '@material-ui/core';
import React, { useContext, useEffect, useState } from 'react'
import Smart_C_Apis from '../../apis/Coading';
import { CodeingContext } from '../../context/CodeingContext';
import Toast from '../../hooks/Toast';
import HeaderStrip from './HeaderStrip';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import '../../scss/MyProjects.scss'
import CloseIcon from '@material-ui/icons/Close';
import { useHistory } from 'react-router-dom';
import Btn from '../../hooks/Btn';

function MyProjects() {
    const history = useHistory();
    const { user, projects, setProjects, addProjectInProjects } = useContext(CodeingContext);

    const [projectName, setprojectName] = useState('');
    const [showCreateProjectCard, setShowCreateProjectCard] = useState(false)

    useEffect(() => {
        if (user.userid) {
            Smart_C_Apis.getProjectsByUserId(user.userid).then(response => {
                if (response.status === 200) {
                    setProjects(response.data);
                } else {
                    Toast.warning(response.message);
                }
            })
        }
    }, [user])

    const createPorject = (e) => {
        e.preventDefault();
        var reqObj = {
            projectName,
            userId: user.userid
        }
        Smart_C_Apis.createProject(reqObj).then(response => {
            if (response.status === 200) {
                Toast.success(response.message);
                addProjectInProjects(response.data);
                setprojectName('');
            } else {
                Toast.warning(response.message);
            }
        })
    } 

    const openProject = (item) => {
        history.push(`/project/start/${item.projectid}`);
    }


    return (
        <div>
            <HeaderStrip title="My Projects"></HeaderStrip>
            <Container>
                <Box py={10}>
                    <Grid container spacing={4}>
                        <Grid item md={4}>
                            <Box container="div" display="flex" alignItems="center">
                                <IconButton color="primary" onClick={() => setShowCreateProjectCard(true)}>
                                    <AddCircleOutlineIcon fontSize="large" ></AddCircleOutlineIcon>
                                </IconButton>
                                <Typography variant="h6"> Add New Project</Typography>
                            </Box>
                            <div className={"layerDivbefore " + (showCreateProjectCard ? 'layerDiv' : '')}>
                                <Card className={"createProjectCard " + (showCreateProjectCard ? 'ShowCreateProjectCard' : '')}>
                                    <IconButton color="secondary" size="small" className="closeButton" onClick={() => setShowCreateProjectCard(false)}>
                                        <CloseIcon fontSize="small"></CloseIcon>
                                    </IconButton>
                                    <Box p={6} component="div">
                                        <form noValidate autoComplete="off" onsubmit={createPorject}>
                                            <Grid container spacing={3} justify="center" direction="row" alignItems="center">
                                                <Grid item md={12}>
                                                    <TextField id="projectName-" label="Project Name"
                                                        placeholder="Project Name" size="small"
                                                        value={projectName} onChange={e=>setprojectName(e.target.value)}
                                                        fullWidth required />
                                                </Grid>
                                                <Grid item>
                                                        <Btn color="red" name="Submit" type="submit"  disabled={!projectName} onClick={createPorject} ></Btn>
                                                </Grid>
                                                {/* <Grid item>
                                                    <Button variant="contained" type="submit" color="primary" disabled={!projectName} 
                                                    onClick={createPorject}>Submit</Button>
                                                </Grid> */}
                                            </Grid>
                                        </form>
                                    </Box>
                                </Card>
                            </div>
                        </Grid>

                        {projects && projects.map((item, index) =>
                            <Grid item md={4} key={index}>
                                <Card className="ProjectCard" onClick={() => openProject(item)}>
                                    <Box p={3}>
                                        <Typography variant="h6" color="initial">
                                            {item.projectname}
                                        </Typography>
                                    </Box>
                                </Card>
                            </Grid>
                        )}


                    </Grid>
                </Box>
            </Container>
        </div>
    )
}

export default MyProjects
