import React, { useRef, useState } from 'react'
import { Button, Box, Grid, TextField, Typography } from '@material-ui/core';
import Btn from '../../hooks/Btn';
import UserApis from '../../apis/User';
import Toast from '../../hooks/Toast';
import ValidationHook from '../../hooks/ValidationHook';

function Register() {
    const [firstname, setFirstname] = useState('')
    const [lastname, setLastname] = useState('')
    const [username, setUsername] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [mobile, setMobile] = useState('')

    const [errors, setErrors] = useState({})
    var getErrors = {};

    const resetForm = () => {
        setFirstname('');
        setLastname('');
        setUsername('');
        setEmail('');
        setPassword('');
        setMobile('')
    }


    const setRegisterErr = (fieldName, value) => {
    var errs = ValidationHook(fieldName, value , errors); 
    console.log("errs", errs);
    setErrors(errs)
    }


    const handeledSubmit = (e) => {
        e.preventDefault();
        if (!firstname) {
            getErrors.firstname = 'First name is required';
        }
        if (!lastname) {
            getErrors.lastname = 'Last name is required';
        }
        if (!username) {
            getErrors.username = 'User name is required';
        }
        if (!email) {
            getErrors.email = 'Email is required';
        }
        if (!password) {
            getErrors.password = 'Password is required';
        }
        if (!mobile) {
            getErrors.mobile = 'Mobile Number is required'; 
        } 

        setErrors(getErrors);
        console.log("Object.keys(getErrors).length", Object.keys(getErrors).length)
        if (Object.keys(getErrors).length !== 0) {
            return null
        }

        var reqObj = {
            firstname, lastname, username, email, password, mobile
        }

        UserApis.createUser(reqObj).then(response=>{
            console.log(response)
            if(response.status == 200){
                Toast.success(response.message);
               resetForm();
            }else{
                Toast.error(response.message)
            }
        })
    }



    

    return (
        <div>
            <Box component="div" mb={5}>
                <Typography variant="h4" display="block" align="center" color="secondary" gutterBottom >
                    Register
                </Typography>
            </Box>
            <form noValidate autoComplete="off"  >
                <Grid container spacing={4} direction="row" justify="center">
                    <Grid item md={5}>
                        <TextField id="outlined-full-width" label="First Name"
                            placeholder="First Name" size="small"
                            value={firstname} onChange={e => { setFirstname(e.target.value); setRegisterErr('firstname', e.target.value) }} fullWidth
                            error={errors?.firstname ? true : false} helperText={errors?.firstname ? errors?.firstname : ''}
                            required />
                    </Grid>
                    <Grid item md={5}>
                        <TextField id="outlined-full-width" label="Last Name"
                            placeholder="Last Name" size="small"
                            value={lastname} onChange={e => { setLastname(e.target.value); setRegisterErr('lastname', e.target.value) }} fullWidth
                            error={errors?.lastname ? true : false} helperText={errors?.lastname ? errors?.lastname : ''}
                            required />
                    </Grid>
                    <Grid item md={10}>
                        <TextField id="outlined-full-width" label="User Name"
                            placeholder="User Name" size="small"
                            value={username} onChange={e => { setUsername(e.target.value); setRegisterErr('username', e.target.value)  }} fullWidth
                            error={errors?.username ? true : false} helperText={errors?.username ? errors?.username : ''}
                            required />
                    </Grid>
                    <Grid item md={10}>
                        <TextField id="outlined-full-width" label="Email"
                            placeholder="Email" size="small"
                            value={email} onChange={e => { setEmail(e.target.value); setRegisterErr('email', e.target.value)  }} fullWidth
                            error={errors?.email ? true : false} helperText={errors?.email ? errors?.email : ''}
                            required />
                    </Grid>
                    <Grid item md={10}>
                        <TextField id="outlined-full-width" label="Password"
                            placeholder="Password" size="small"
                            value={password} onChange={e => { setPassword(e.target.value); setRegisterErr('password', e.target.value)  }} fullWidth
                            error={errors?.password ? true : false} helperText={errors?.password ? errors?.password : ''}
                            required />
                    </Grid>
                    <Grid item md={10}>
                        <TextField id="outlined-full-width" label="Mobile Number"
                            placeholder="Mobile Number" size="small"
                            value={mobile} onChange={e => { setMobile(e.target.value); setRegisterErr('mobile', e.target.value)  }} fullWidth
                            error={errors?.mobile ? true : false} helperText={errors?.mobile ? errors?.mobile : ''}
                            required />
                    </Grid>
                    <Grid item md={10}>
                        <Box textAlign="center" my={2}>
                            <Btn color="red" name="Register" onClick={handeledSubmit}></Btn>
                            {/* disabled={!firstname || !lastname || !username || !email || !password || !mobile} */}
                        </Box>
                    </Grid>

                </Grid>
            </form>
        </div>
    )
}

export default Register
