import { Box } from '@material-ui/core';
import React, { useContext, useState, useEffect } from 'react';
import { CodeingContext } from '../../../context/CodeingContext';
import CodeEditor from './CodeEditor';
import CloseIcon from '@material-ui/icons/Close';
import addEditorTabsAndContent from '../../../hooks/SetEditorTabsAndContent';
import { makeStyles } from '@material-ui/styles';
import '../../../scss/codepannel.scss';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%'
    },
}));

var tabsList = [];
function CodePannel() {

    const classes = useStyles();
    const [value, setValue] = useState(0);
    const { tabs, setTabs, file, setFile } = useContext(CodeingContext);
    // const [file, setFile] = useState({})

    useEffect(() => {
            tabs.forEach((tab, index) => {
                tab.mode = getFileType(tab.fileName);
                if (tab.active) {
                    setValue(index);
                    setFile(tab);
                }
            });
  
        
        tabsList = tabs;

        if(tabsList.length == 0){
            setFile({mode:'html', name:'undefined'})
        }
    }, [tabs])



    const closeFile = (file, index) => {
        tabsList.splice(index, 1);
        var files = addEditorTabsAndContent.removeTabs(file);
        setTabs(files);
    }

    const getFileType = (fileName) => {
        if (fileName) {
            var extensions = fileName.split('.');
            var extension = extensions[extensions.length - 1];
            if (extensions[extensions.length - 1] == 'js') {
                extension = 'javascript'
            }
            return extension;
        }
        return 'html'
    }

   const selectFile = (tabindex) =>{
    tabsList.forEach((tab , index)=>{
        tab.active = false;
        if(tabindex === index){
            tab.active = true; 
        }
    })
    setTabs(tabsList);
   }

    return (
        <div className={classes.root + ' myCodepannel'}>
            <Box component="div" display="flex" className="topbarcontainer">
                {tabs && tabs.map((item, index) =>
                    <Box key={index} component="div" display="flex" justifyContent="space-between"
                        alignItems="center" className={`tabs ${item.active ? "active" : ""}`}>
                        <Box mr={2} onClick={()=>{setFile(item); selectFile(index)}}>{item.fileName}</Box>
                        <CloseIcon fontSize="small" className="closeIcon" onClick={() => closeFile(item, index)} />
                    </Box>
                )}
            </Box>
            <Box component="div">
                <CodeEditor file={file}></CodeEditor>
            </Box>
        </div >
    )
}

export default CodePannel;
