import { get, post, update, remove } from './Methods'

const baseURL = 'http://localhost:3006/api/v1/compile';

const CompilerApis = {
    compile:Function
}


CompilerApis.compile = (body) => {
    let url = baseURL + '/run';
    return post(url, body).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

export default CompilerApis;