import { get, post, update, remove } from './Methods'

const baseURL = 'http://localhost:3006/api/v1/smart_c';

const Smart_C_Apis = {
    createProject: Function,
    getProjects: Function,
    getProjectsByUserId: Function,
    getProjectById: Function,
    updateProject: Function,
    deleteProject: Function,

    createFolder: Function,
    getFolders: Function,
    updateFolder: Function,
    deleteFolder: Function,

    createFile: Function,
    getFiles: Function,
    updateFile: Function,
    deleteFile: Function,
    updateFileContent: Function
}

//============= Project start ================

Smart_C_Apis.createProject = (body) => {
    let url = baseURL + '/create/project';
    return post(url, body).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

Smart_C_Apis.getProjects = () => {
    let url = baseURL + '/project/all';
    return get(url).then(response => response).catch(error => {
        console.error('There was an error!', error);
    });
}


Smart_C_Apis.getProjectsByUserId = (id) => {
    let url = baseURL + '/projects/' + id;
    return get(url).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

Smart_C_Apis.getProjectById = (id) => {
    let url = baseURL + '/project/' + id;
    return get(url).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

Smart_C_Apis.updateProject = (body, id) => {
    let url = baseURL + '/project/' + id;
    return update(url, body).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

Smart_C_Apis.deleteProject = (id) => {
    let url = baseURL + '/project/' + id;
    return remove(url).then(response => response)
        .catch(error => {
            console.error('There was an error!', error);
        });
}

//================Projects end=============


