import React, { useState } from 'react'
import { Box, Grid, TextField, Typography } from '@material-ui/core';
import Btn from '../../hooks/Btn';
import UserApis from '../../apis/User';
import Toast from '../../hooks/Toast';
import ValidationHook from '../../hooks/ValidationHook';
import UserInfo from '../../hooks/UserInfo';
import { useHistory } from 'react-router-dom';


function Login() {
    const hustory = useHistory();
    const [userId, setUserId] = useState('');
    const [password, setPassword] = useState('');

    const [errors, setErrors] = useState({});
    var getErrors = {};

    const setLoginErr = (fieldName, value) => {
        var errs = ValidationHook(fieldName, value, errors);
        setErrors(errs)
    }

    const handeledSubmit = (e) => {
        e.preventDefault();

        if (!userId) {
            getErrors['Email'] = 'Email is required';
        }
        if (!password) {
            getErrors['password'] = 'password is required';
        }
        setErrors(getErrors);
        if (Object.keys(getErrors).length !== 0) {
            return null
        }

        var reqObj = {
            userId, password
        }
        UserApis.login(reqObj).then(response => {
            if (response.status == 200) {
                Toast.success(response.message);
                UserInfo.setUser(response.data);
                hustory.push('/logged-in/dashboard');
            } else {
                Toast.error(response.message);
            }
        })
    }


    return (
        <div>
            <Box component="div" mb={5}>
                <Typography variant="h4" display="block" align="center" color="primary" gutterBottom >
                    Login
            </Typography>
            </Box>
            <form noValidate autoComplete="off">
                <Grid container spacing={4} direction="row" justify="center">
                    <Grid item md={10}>
                        <TextField id="outlined-full-width" label="Email"
                            placeholder="Email" size="small"
                            value={userId} onChange={e => { setUserId(e.target.value); setLoginErr('userId', e.target.value) }}
                            error={errors?.userId ? true : false} helperText={errors?.userId ? errors?.userId : ''}
                            fullWidth required />
                    </Grid>
                    <Grid item md={10}>
                        <TextField id="outlined-full-widths" label="Password"
                            placeholder="Password" size="small"
                            value={password} onChange={e => { setPassword(e.target.value); setLoginErr('password', e.target.value) }}
                            error={errors?.password ? true : false} helperText={errors?.password ? errors?.password : ''}
                            onKeyUpCapture={(e)=> e.key === "Enter"? handeledSubmit(e) : ''}
                            fullWidth required/>
                    </Grid>
                    <Grid item md={10}>
                        <Box textAlign="center" my={2}>
                            <Btn color="blue" name="Login" onClick={handeledSubmit} ></Btn>
                        </Box>
                    </Grid>

                </Grid>
            </form>
        </div>
    )
}

export default Login
