import React, { createContext, useState, useEffect, useRef } from "react";
import { io } from 'socket.io-client';
import Peer from 'simple-peer';
import { ScreenShareTwoTone } from "@material-ui/icons";

export const SocketContext = createContext();
const socket = io('http://localhost:3006/');


function userMediaAvailable() {
    return !!(navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia);
}

function getUserMedia() {
    if (userMediaAvailable()) {
        return navigator.mediaDevices.getUserMedia({ video: true, audio: true })
    }
    else {
        throw new Error('User media not available');
    }
}
function shareScreen() {
    if (userMediaAvailable()) {
        return navigator.mediaDevices.getDisplayMedia({
            video: {
                cursor: "always"
            },
            audio: {
                echoCancellation: true,
                noiseSuppression: true,
                sampleRate: 44100
            }
        });
    }
    else {
        throw new Error('User media not available');
    }
}

export const SocketContextProvider = ({ children }) => {
    const [mySocketId, setMySocketId] = useState("");
    const [name, setName] = useState("");
    const [userStream, setUserStream] = useState(null);
    const [audio, setAudio] = useState(true);
    const [video, setVideo] = useState(true);

    const [screenShareCall, setScreenShareCall] = useState(null);

    const myVideo = useRef();
    const screenShareVideo = useRef();
    const connectionRef = useRef();

    // const [receiverScreenShareCall, setReceiverScreenShareCall] = useState(false);
    // const [me, setMe] = useState("");

    // const [call, setCall] = useState(null);
    // const [callAccepted, setCallAccepted] = useState(false);
    // const [callEnded, setCallEnded] = useState(false);




    useEffect(() => {
        socket.on('mySicketId', (id) => {
            console.log("mySocketId===> ", id);
            setMySocketId(id);
        });

        socket.on('getscreenshare', (data) => {
            setScreenShareCall(data);
        });

    }, []);

    const ActivateMyVideo = () => {
        getUserMedia().then((stream) => {
            setUserStream(stream);
            myVideo.current.srcObject = stream;
        })
    }
    const videoOnOff = () => {
        userStream && userStream.getVideoTracks().forEach(track => track.enabled = !track.enabled);
        setVideo(!video);
    }
    const audioOnOff = () => {
        userStream && userStream.getAudioTracks().forEach(track => track.enabled = !track.enabled);
        setAudio(!audio);
    }

    const shareMyScreenScreen = (projectId) => {
        shareScreen().then((stream) => {
            const peer = new Peer({ initiator: true, trickle: false, stream });
            peer.on('signal', (data) => {
                socket.emit('setsharescreen', {
                    projectId: projectId,
                    signal: data,
                    socketId: mySocketId,
                    name: name
                })
            })
            socket.on('acceptscreenshare', (signal) => {
                peer.signal(signal)
            })
            connectionRef.current = peer;
        })
    }


    const joinScreenShare = (projectId) => {
        if (screenShareCall) {
            const peer = new Peer({ initiator: false, trickle: false });
            peer.on('signal', (data) => {
                socket.emit('joinscreenshare', {
                    signal: data,
                    socketId: screenShareCall.socketId,
                    projectId: projectId
                });
            })
            peer.on('stream', (currentStream) => {
                screenShareVideo.current.srcObject = currentStream;
            });
            peer.signal(screenShareCall.signal);
            connectionRef.current = peer;
        }
    }

    // const leaveCall = () => {
    //     setCallEnded(true);
    //     connectionRef.current.destroy();
    //     window.location.reload();
    // }

    return (
        <SocketContext.Provider value={{
            myVideo,
            ActivateMyVideo,
            userStream,
            name, setName,
            audio, setAudio,
            video, setVideo,
            videoOnOff,
            audioOnOff,
            shareMyScreenScreen,
            screenShareVideo,
            joinScreenShare
        }} >
            {children}
        </SocketContext.Provider>
    )


}



