import React, { useEffect, useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { useParams } from "react-router-dom";
import Smart_C_Apis from '../../../apis/Coading';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';
import InsertDriveFileOutlinedIcon from '@material-ui/icons/InsertDriveFileOutlined';
import { Box, IconButton, Tooltip, TextField, InputAdornment, Input, FormControl, Menu, MenuItem } from '@material-ui/core';
import Toast from '../../../hooks/Toast';
import Delete from '@material-ui/icons/Delete';
import CreateIcon from '@material-ui/icons/Create';
import RefreshIcon from '@material-ui/icons/Refresh';
import addEditorTabsAndContent from '../../../hooks/SetEditorTabsAndContent';
import { CodeingContext } from '../../../context/CodeingContext';

import TreeView from '@material-ui/lab/TreeView';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';


const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexShrink: 0,
    display: 'block !important',
    overflow: "hidden",
    width: '90%',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
  },

}));

var TotalProject;
function FileStructure() {
  const classes = useStyles();
  const { id } = useParams();

  const { tabs, setTabs } = useContext(CodeingContext);

  const [Project, setProject] = useState({});
  //=====create folder
  const [folderName, setfolderName] = useState('');
  const [projectId, setProjectId] = useState(id);
  const [parentFolderId, setparentFolderId] = useState('');
  //=====create file
  const [fileName, setFileName] = useState('');
  const [fileEx, setFileEx] = useState('');
  const [showFolderFiled, setShowFolderFiled] = useState(false);
  const [showFileField, setShowFileField] = useState(false);
  const [fileOrfolder, setFileOrFolder] = useState('');
  const [renameFolderOrFile, setRenameFolderOrFile] = useState(null);


  useEffect(() => {
    getProjectData();

    var files = addEditorTabsAndContent.addAndGetFile(null);
    console.log("files", files);
    setTabs(files);
  }, [])

  const getProjectData = () => {
    Smart_C_Apis.getProjectById(id).then(response => {
      if (response.status === 200) {
        TotalProject = response.data;
        console.log(TotalProject)
        TotalProjectRefresh();
      }
    })
  }

  const TotalProjectRefresh = () => {
    TotalProject.folders && TotalProject.folders.sort((a, b) => b.folderId - a.folderId).map(pfold => {
      pfold.addFolder = false;
      pfold.addFile = false;
      pfold.renameFolder = false;
    });

    TotalProject.files && TotalProject.files.sort((a, b) => b.fileId - a.fileId).map(pfile => {
      pfile.renameFile = false;
    });

    setShowFolderFiled(false);
    setShowFileField(false);
    arrageFileStructure(JSON.parse(JSON.stringify(TotalProject)));
  }


  const arrageFileStructure = (c_project) => {
    c_project.folders && c_project.folders.map(folder => {
      folder.files = [];
      c_project.files && c_project.files.map(file => {
        if (file.folderId == folder.folderId) {
          folder.files.push(file);
        }
      })
    })
    c_project.folders && c_project.folders.map(pfold => {
      pfold.folders = [];
      c_project.folders.map(folder => {
        if (pfold.folderId === folder.parentFolderId) {
          pfold.folders.push(folder);
        }
      })
    })
    c_project.folders = c_project.folders && c_project.folders.filter(x => !x.parentFolderId);
    setProject(c_project);
  }


  const addFolderOrFile = (type) => {
    setfolderName('');
    setFileName('');
    setShowFolderFiled(false);
    setShowFileField(false);
    if (!parentFolderId) {
      if (type === 'folder') {
        setShowFolderFiled(true);
      } else {
        setShowFileField(true);
      }
    } else {
      if (type === 'folder') {
        TotalProject.folders && TotalProject.folders.map(pfold => {
          pfold.addFile = false;
          pfold.renameFile = false;
          pfold.renameFolder = false;
          pfold.folderId == parentFolderId ? pfold.addFolder = true : pfold.addFolder = false;
        })
      } else {
        TotalProject.folders && TotalProject.folders.map(pfold => {
          pfold.addFolder = false;
          pfold.renameFile = false;
          pfold.renameFolder = false;
          pfold.folderId == parentFolderId ? pfold.addFile = true : pfold.addFile = false;
        })
      }
    }
    arrageFileStructure(JSON.parse(JSON.stringify(TotalProject)));
  }

  const RenameFolderOrFile = (type) => {
    console.log("renameFolderOrFile", renameFolderOrFile)
    if (!renameFolderOrFile) {
      // if (type === 'folder') {
      //     setShowFolderFiled(true);
      // } else {
      //     setShowFileField(true);
      // }
    } else {
      if (type === 'folder') {
        TotalProject.folders && TotalProject.folders.map(pfold => {
          pfold.renameFolder = false;
          pfold.addFolder = false;
          pfold.folderId == renameFolderOrFile ? pfold.renameFolder = true : pfold.renameFolder = false;
        })
      } else {
        TotalProject.files && TotalProject.files.map(pfold => {
          pfold.renameFile = false;
          pfold.addFile = false;
          pfold.fileId == renameFolderOrFile ? pfold.renameFile = true : pfold.renameFile = false;
        })
      }
    }

    console.log("TotalProject", TotalProject)

    arrageFileStructure(JSON.parse(JSON.stringify(TotalProject)));
  }


  const createFolder = () => {
    var reqBody = {
      folderName,
      projectId,
      parentFolderId: parentFolderId ? parentFolderId : null
    }
    console.log("----> create--", reqBody);
    Smart_C_Apis.createFolder(reqBody).then(response => {
      console.log('createFolder==>', response)
      if (response.status === 200) {
        getProjectData();
        setparentFolderId('');
        setfolderName('')
        Toast.success(response.message);
      } else {
        Toast.error(response.message);
      }
    })
  }

  const updateFolder = () => {
    var reqBody = {
      folderName
    }
    console.log("----> update--", reqBody);
    Smart_C_Apis.updateFolder(reqBody, renameFolderOrFile).then(response => {
      console.log('updateFolder==>', response)
      if (response.status === 200) {
        getProjectData();
        setRenameFolderOrFile('');
        setfolderName('');
        setFileOrFolder('');
        Toast.success(response.message);
      } else {
        Toast.error(response.message);
      }
    })
  }

  const deleteFileOrFolders = () => {
    var deleteFolderOrFileis = null;
    if (fileOrfolder === 'folder') {
      deleteFolderOrFileis = Smart_C_Apis.deleteFolder(renameFolderOrFile)
    } else {
      deleteFolderOrFileis = Smart_C_Apis.deleteFile(renameFolderOrFile)
    }

    deleteFolderOrFileis.then(response => {
      console.log('deleteFolder==>', response)
      if (response.status === 200) {
        getProjectData();
        setRenameFolderOrFile('');
        setFileOrFolder('');
        Toast.success(response.message);
      } else {
        Toast.error(response.message);
      }
    })
  }

  const createFile = () => {
    console.log("hello-----")

    var reqBody = {
      fileName,
      projectId,
      folderId: parentFolderId ? parentFolderId : null,
      fileEx,
      fileContent: ''
    }
    console.log("----> create--", reqBody);
    Smart_C_Apis.createFile(reqBody).then(response => {
      console.log('createFolder==>', response)
      if (response.status === 200) {
        getProjectData();
        Toast.success(response.message);
      } else {
        Toast.error(response.message);
      }
    })
  }

  const updateFile = () => {
    var reqBody = {
      fileName
    }
    console.log("----> update--", reqBody);
    Smart_C_Apis.updateFile(reqBody, renameFolderOrFile).then(response => {
      console.log('updateFolder==>', response)
      if (response.status === 200) {
        getProjectData();
        setRenameFolderOrFile('');
        setFileName('');
        setFileOrFolder('');
        Toast.success(response.message);
      } else {
        Toast.error(response.message);
      }
    })
  }



  const AddFileToTabs = (file) => {
    var files = addEditorTabsAndContent.addAndGetFile(file);
    console.log("files", files);
    setTabs(files);
  }


  const folderField = (operation) => <FormControl>
    <Input id="foldername" autoFocus={true}
      placeholder="FolderName" size="small"
      value={folderName}
      onChange={e => { setfolderName(e.target.value) }} required
      onKeyUpCapture={(e) => e.key === "Enter" ? operation() : ''}
    />
  </FormControl>;

  const fileField = (operation) => <FormControl>
    <Input id="filename" autoFocus={true}
      placeholder="FileName" size="small"
      value={fileName}
      onChange={e => { setFileName(e.target.value) }} required
      onKeyUpCapture={(e) => e.key === "Enter" ? operation() : ''}
    />
  </FormControl>

  const structure = (folders) => {
    return folders.map((_fold, folderIndex) =>
      <div key={_fold.folderId}>
        <TreeItem nodeId={_fold.folderId.toString()}
          label={_fold.renameFolder ? folderField(updateFolder) : _fold.folderName}
          onLabelClick={() => {
            setparentFolderId(_fold.folderId);
            setRenameFolderOrFile(_fold.folderId);
            setfolderName(_fold.folderName);
            setFileOrFolder('folder')
          }}
        >

          {_fold.folders && structure(_fold.folders, true)}
          {_fold.files && _fold.files.map((file, fileIndex) =>
            <div key={file.fileId} className="fileOrFolder folderCardition">
              <TreeItem nodeId={file.fileId.toString()}
                label={file.renameFile ? fileField(updateFile) : file.fileName}
                onLabelClick={() => {
                  setparentFolderId(file.folderId);
                  setRenameFolderOrFile(file.fileId);
                  setFileName(file.fileName);
                  setFileOrFolder('file')
                  AddFileToTabs(file)
                }}
              ></TreeItem>
            </div>
          )}
          {_fold.addFolder ? folderField(createFolder) : ''}
          {_fold.addFile ? fileField(createFile) : ''}

        </TreeItem>
      </div>
    )
  }


  return (
    <div className={classes.root + ' ProjectView'} >
      <Box component="div" pl={1}>
        <Typography variant="h6">
          {Project.projectname}
        </Typography>
      </Box>
      <Box component="div" display="flex" justifyContent="flex-end">
        <Tooltip title="Folder">
          <IconButton color="primary" size="small" className="createBtn"
            onClick={() => addFolderOrFile('folder')}>
            <FolderOpenIcon fontSize="small"></FolderOpenIcon>
          </IconButton>
        </Tooltip>
        <Tooltip title="File">
          <IconButton color="primary" size="small" className="createBtn"
            onClick={() => addFolderOrFile('file')}>
            <InsertDriveFileOutlinedIcon fontSize="small"></InsertDriveFileOutlinedIcon>
          </IconButton>
        </Tooltip>

        <Tooltip title="Refresh">
          <IconButton color="primary" size="small" className="createBtn"
            onClick={() => TotalProjectRefresh()}>
            <RefreshIcon fontSize="small"></RefreshIcon>
          </IconButton>
        </Tooltip>
        <Tooltip title="Edit">
          <IconButton color="primary" size="small" className="createBtn"
            onClick={() => RenameFolderOrFile(fileOrfolder)}>
            <CreateIcon fontSize="small"></CreateIcon>
          </IconButton>
        </Tooltip>
        <Tooltip title="Delete">
          <IconButton color="secondary" size="small" className="createBtn"
            onClick={() => deleteFileOrFolders()}>
            <Delete fontSize="small"></Delete>
          </IconButton>
        </Tooltip>

      </Box>


      <Box component="div" px={2} >
        <Box component="div" className="tree">
          <TreeView className="" defaultCollapseIcon={<ExpandMoreIcon />}
            defaultExpandIcon={<ChevronRightIcon />}>
            {Project && Project.folders && structure(Project.folders)}
          </TreeView>
        </Box>
        {showFolderFiled ? folderField(createFolder) : ''}
        {showFileField ? fileField(createFile) : ''}
      </Box>
      <Box component="div" border px={2} height="100%" onClick={() => { TotalProjectRefresh(); setparentFolderId('') }}>
      </Box>
    </div>
  );
}
export default FileStructure
