import React, { useContext, useEffect, useState } from 'react';
import Navbar from '../components/Logged-In/Navbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

import Dashboard from '../components/Logged-In/Dashboard';
import Profile from '../components/Logged-In/Profile';
import MyProjects from '../components/Logged-In/MyProjects';

import { useHistory } from 'react-router-dom';
import UserInfo from '../hooks/UserInfo'
import { CodeingContext } from '../context/CodeingContext';
import BlockCodeing from '../components/Logged-In/BlockCodeing';


function LoggedIn() {
    const history = useHistory();
    const { user, setUser } = useContext(CodeingContext);


    useEffect(() => {
        if (!UserInfo.isLoggedIn()) {
            history.push('/');
        }
        setUser(UserInfo.getUser());
        console.log("user", user);

    }, [])


    return (
        <div>
            <Navbar></Navbar>
            <Route exact path='/logged-in/dashboard'>
                <Dashboard />
            </Route>
            <Route path='/logged-in/profile'>
                <Profile />
            </Route>
            <Route path='/logged-in/my-projects'>
                <MyProjects />
            </Route>
            <Route path='/logged-in/block-codeing'>
                <BlockCodeing />
            </Route>

        </div>
    )


}

export default LoggedIn
