var CryptoJS = require("crypto-js");

const EncrDecr = {
    set: Function,
    get: Function
}

EncrDecr.set = (value) => {
    return CryptoJS.AES.encrypt(value, '!#--"Smart_C"--#!')
}

EncrDecr.get = (value) => {
    return CryptoJS.AES.decrypt(value, '!#--"Smart_C"--#!').toString(CryptoJS.enc.Utf8);
}

export default EncrDecr;